<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
        <FormItem label="检查通知书号：">
          <label>{{watchTableCell.val1}}</label>
        </FormItem>
        <FormItem label="检查时间：">
          <label>{{watchTableCell.val2[0]}}~{{watchTableCell.val2[1]}}</label>
        </FormItem>
        <FormItem label="主查单位：">
          <label>{{watchTableCell.val3}}</label>
        </FormItem>
        <FormItem label="被查单位：">
          <label>{{watchTableCell.val4}}</label>
        </FormItem>
        <FormItem label="对机构处罚金额(人民币万元)：">
          <label>{{watchTableCell.val5}}</label>
        </FormItem>
        <FormItem label="对个人处罚金额(人民币万元)：">
          <label>{{watchTableCell.val6}}</label>
        </FormItem>
        <FormItem label="处罚人数：">
          <label>{{watchTableCell.val7}}</label>
        </FormItem>
        <FormItem label="检查中发现的主要问题：">
          <label>
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val8"
            >
              <p slot="reference" class="textMaxWidth2 disBlock textOverflow">{{watchTableCell.val8}}</p>
            </el-popover>
          </label>
        </FormItem>
        <FormItem label="整改情况">
          <label>
            <!-- {{watchTableCell.val9}} -->
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val9"
            >
              <p slot="reference" class="textMaxWidth2 disBlock textOverflow">{{watchTableCell.val9}}</p>
            </el-popover>
          </label>
        </FormItem>
        <FormItem label="是否公开：">
          <label>{{watchTableCell.val10?'是':'否'}}</label>
        </FormItem>
        <FormItem label="附件：">
          <showUsualFiles :file-obj="dataObj4" :key="fileId4"></showUsualFiles>
        </FormItem>
        <FormItem label="追加附件：">
          <showUsualFiles :file-obj="dataObj5" :key="fileId5"></showUsualFiles>
        </FormItem>
        <FormItem label="追加内容：">
          <label>
            <!-- {{watchTableCell.val11}} -->
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val1"
            >
              <p slot="reference" class="textMaxWidth2 disBlock textOverflow">{{watchTableCell.val11}}</p>
            </el-popover>
          </label>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      addPeopleArr: [], //被调查人
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      this.axios
        .post(`/process/infCheck/getEntityRow/${val3}` + "?infViewNum=1")
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.val1 = response.data.fileNum;
            this.watchTableCell.val2 = [
              response.data.checkStartTime,
              response.data.checkEndTime
            ];
            this.watchTableCell.val3 = response.data.implementUnit;
            this.watchTableCell.val4 = response.data.inspectedUnit;
            this.watchTableCell.val5 = response.data.penaltyAmountOrg;
            this.watchTableCell.val6 = response.data.penaltyAmountPerson;
            this.watchTableCell.val7 = response.data.punishedNum;
            this.watchTableCell.val8 = response.data.findProblems;
            this.watchTableCell.val9 = response.data.reformResult;
            this.watchTableCell.val10 = JSON.parse(response.data.ifShow);
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = !this.fileId4;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = !this.fileId5;
          }
        })
        .catch(function(error) {});
      // ---------------
    }
  }
};
</script>


